<template>
  <div class="price-card">
    <h3></h3>
    <div class="content">
      <el-table
        :data="tableData"
        :span-method="objectSpanMethod"
        header-row-class-name="tableHeader"
        row-class-name="tableRow"
        style="width: 100%">
        <el-table-column
          prop="date"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="address"
          label="">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  name: 'price',
  data () {
    return {
      tableData: [{
        date: ' ',
        name: '1',
        address: ' 1518  1518 1518'
      }, {
        date: '2016-05-04',
        name: '2',
        address: ' 2222 '
      }, {
        date: '2016-05-01',
        name: '3',
        address: ' 3333 '
      }, {
        date: '2016-05-03',
        name: '4',
        address: ' 4444 '
      }, {
        date: '2016-05-03',
        name: '5',
        address: ' 5555 '
      }, {
        date: '2016-05-03',
        name: '6',
        address: ' 6666 '
      }, {
        date: '2016-05-03',
        name: '7',
        address: ' 7777 '
      }, {
        date: '2016-05-03',
        name: '7',
        address: ' 7777 '
      }, {
        date: '2016-05-03',
        name: '7',
        address: ' 7777 '
      }, {
        date: '2016-05-03',
        name: '7',
        address: ' 7777 '
      }, {
        date: '2016-05-03',
        name: '7',
        address: ' 7777 '
      }, {
        date: '2016-05-03',
        name: '7',
        address: ' 7777 '
      }]
    }
  },
  methods: {
    objectSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex === 0) {
          return {
            rowspan: 4,
            colspan: 1
          }
        } else if (rowIndex >= 0 && rowIndex < 4) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    }
  }
}
</script>
<style scoped>
  .price-card h3 {
    text-align: left;
    width: 80px;
    height: 30px;
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 30px;
  }
  .el-table >>> .tableHeader {
    color: #5e6166;
    /*font-size: 14px;*/
  }
  .el-table >>> th.is-leaf {
    background-color: #f7f7f7;
    /*line-height: 23px;*/
  }
  .el-table >>> .tableRow {
    text-align: left;
  }
  .el-table >>> .tableRow:nth-child(-n+3) td {
    border-bottom: 0;
  }
  .el-table >>> .tableRow:nth-child(5) td:first-child {
    border-top-color: #ebeef5;
    border-top-style: solid;
    border-top-width: 1px;
  }
</style>



